const DetailColumn = styled.div`
    grid-row: 1;
    /* grid-column: 2; */

    display: grid;
    text-align: left;
`;

const DateRow = styled.div`
    display: grid;
    grid-template-columns: 3fr 1fr;
    margin-top: 10px;
    background: green;

    p {
        margin: 0;
        font-family: 'QuicksandBold';

        :nth-child(1) {
            background: red;
        }

        :nth-child(2) {
            background: blue;
            text-align: right;
        }
    }

    @media ${device.mobile} {
        grid-template-columns: 1fr;
        grid-auto-rows: minmax(10px, auto);

        p {
            :nth-child(1) {
                order: 2;
            }

            :nth-child(2) {
                order: 1;
            }
        }
    }
`;

const BulletRow = styled.li`
    list-style-type: square;
    margin-left: 18px;
`;

export default () => (
    <section>
        <CenteredHeader>Experiences</CenteredHeader>
        <DetailColumn>
            {info.experiences.map((exp, index) => (
                <div key={index}>
                    <DateRow>
                        <p>{exp.title}</p>
                        <p>{exp.rightContent}</p>
                    </DateRow>
                    <ul>
                        {exp.bullets.map((bullet, idx) => (
                            <BulletRow key={idx}>{bullet}</BulletRow>
                        ))}
                    </ul>
                </div>
            ))}
        </DetailColumn>
    </section>
);
